<template>
  <div>
    <vs-table striped class=" mt-5">
      <template #header>
        <vs-button class=" focus:outline-none" @click="importarDatos()"
          >Guardar Datos</vs-button
        >
      </template>

      <template #thead>
        <vs-tr>
          <vs-th v-for="(item, i) in columns" :key="i">
            {{ item.text }}
          </vs-th>
        </vs-tr>
      </template>

      <template #tbody>
        <vs-tr :key="i" v-for="(tr, i) in dataListLocal" :data="tr">
          <vs-td
            @click="$emit('edit', tr)"
            :edit="i2 == 0"
            v-for="(item, i2) in columns"
            :key="i2"
          >
            {{ tr[item.name] }}
          </vs-td>
        </vs-tr>
      </template>

      <template #notFound>
        No exiten datos 
      </template>
    </vs-table>
  </div>
</template>

<script>
import axios from "@//plugins//axios.js";

export default {
  props: {
    dataListLocal: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  data() {
    return {
      columns: [
        {
          name: "postulante",
          text: "Id Postulante",
        },

        {
          name: "pabellon",
          text: "Pabellon",
        },

        {
          name: "aula",
          text: "Aula",
        },

        {
          name: "asiento",
          text: "Asiento",
        },
      ],
    };
  },

  methods: {
    async importarDatos() {
      if (this.dataListLocal.length <= 0) return;

      const loading = this.$vs.loading();
      try {
        const { status } = await axios.post(
          "postulante_aula/",
          this.dataListLocal
        );
        if (status != 200 || status != 201) {
          this.$vs.notification({
            title: "Importaci贸n Finalizada",
            text: "Ocurrio un problema! ",
            color: "danger",
          });
        }

        this.$vs.notification({
          title: "Importaci贸n Finalizada",
          text: "La importaci贸n Finali贸 con exito! ",
          color: "success",
        });
      } catch (error) {
        this.$vs.notification({
          title: "Importaci贸n Finalizada",
          text: "Ocurrio un problema! ",
          color: "danger",
        });
      } finally {
        loading.close();
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
